* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*::before {
  box-sizing: border-box;
}
*::after {
  box-sizing: border-box;
}
#html {
  word-break: break-all;
}
#div1 {
  position: fixed;
  right: 30%;
  top: 20px;
  transform: translateX(50%);
}
#div1::before {
  content: '';
  display: block;
  position: absolute;
  border-radius: 50%;
}
#div1::after {
  content: '';
  display: block;
  position: absolute;
  border-radius: 50%;
}
@media (max-width: 500px) {
  #html {
    height: 50vh;
    overflow: auto;
  }
  #div1Wrapper {
    height: 50vh;
    position: relative;
  }
  #div1 {
    position: absolute;
    top: 30px;
    right: 50%;
    transform: translateX(50%);
  }
}
